<template>
  <div>
    <div class="loadList" v-loading="listloading" element-loading-text="">
      <div v-if="!loading&&dataList.length<=0&&currentPage===0">
        <div>
          <ul class="p-list-con">
            <p class="err-tip"></p>
          </ul>
        </div>
      </div>
      <div v-else>
        <slot name="item"
              v-for="(item,index) in this.dataList"
              :index="index"
              :obj="item">
          <!--  -->
        </slot>
        <div style="clear:both;"></div>
      </div>

    </div>
    <div class="load-more" :style="css_loadmore" v-if="!(!loading&&dataList.length<=0&&currentPage===0)">
      <div v-if="mode=='more'">
        <el-button v-if="loading" type="success" icon="loading"></el-button>
        <el-button v-else-if="!loading&&!hasNextPage" type="info" @click="message.info('')"></el-button>
        <el-button v-else-if="!loading&&hasNextPage" type="primary" @click="loadMore()"></el-button>
      </div>
      <div v-else-if="mode=='pen'&&!loading">
        <el-pagination style="text-align: center" v-if="!(!loading&&totalPage===1&&currentPage===1)"
                       layout="prev, pager, next"
                       :current-page="currentPage" :page-count="totalPage"
                       @size-change="handleSizeChange"
                       @current-change="handleCurrentChange">
        </el-pagination>
      </div>
      <div v-else-if="mode=='infinite'">
        <el-button v-if="isMore" type="success" icon="loading">...</el-button>
        <el-button v-else-if="!hasNextPage" type="info" @click="message.info('')"></el-button>
      </div>
      <div v-else-if="mode=='none'" style="display: none"></div>
    </div>
  </div>

</template>

<script>
  import {Loading, pagination} from "element-ui"
  import Vue from  "vue"
  Vue.use(Loading);
  Vue.use(pagination);
  export default {
    mixins: [],
    components: {},
    data(){
      return {
        dataList: this.data_list,//
        currentPage: 0,//
        limit: 4,//
        total: 0,//
        totalPage: this.total_page,//
        loading: false,
        isMore: false,
        listloading: false,//
      }
    },
    watch: {
      data_list: function (newVal) {
        switch (this.mode) {
          case 'more':
            this.stopLoading();
            this.pushToList(newVal)
            break;
          case 'infinite':
            this.stopLoading();
            this.pushToList(newVal);
            break;
          case 'pen':
            this.stopLoading();
            this.coverToList(newVal)
            break;
          case 'none':
            this.stopLoading();
            this.pushToList(newVal);
            break;
        }

      },
      total_page: function (newVal) {
        this.totalPage = newVal;
      }
    },
    props: {data_list: {require: true}, total_page: {require: true}, mode: {default: 'infinite'}, css_loadmore: {}},
    computed: {
      //
      hasNextPage() {
        return this.currentPage < this.totalPage;
      },
    },
    created(){
      this.initData();
    },
    mounted() {
      if (this.mode == 'infinite') {
        window.addEventListener('scroll', this.debounce(this.doCheck), 250);
      }
    },
    methods: {
      /**
       *  index() limit() ()
       * @param params
       * @returns {*}
       */
      getParams (params) {
        return Object.assign({
          paging: true,
          pageNum: this.currentPage,
          limit: this.limit
        }, params)
      },
      /**
       * 
       */
      loadMore () {
//        console.log("");
        this.currentPage++;
        this.loadData();
      },
      /**
       * 
       */
      toPage(pagenum){
        this.currentPage = pagenum;
        this.loadData();
      },
      /**
       * 
       */
      coverToList(dataList){
        if (dataList && dataList.length > 0) {
          this.dataList = dataList;
        } else {
          this.currentPage--;
//          this.message.info("")
        }
      },
      /**
       * list vue push  filter
       * @param list
       * @param filter
       */
      pushToList (dataList, filter) {
        if (dataList && dataList.length > 0) {
          dataList.forEach((item) => {
            if (typeof filter === 'function') {
              this.dataList.push(filter(item))
            } else {
              this.dataList.push(item)
            }
          })
        } else {
          this.currentPage--;
//          this.message.info("")
        }
      },
      /**
       * 
       */
      initData () {
        this.currentPage = 1
        this.dataList = [];
        this.loadData()
      },
      /**
       * @overwrite
       *  mixin 
       */
      loadData () {
        this.startLoading();
        this.$emit("loadData", this.getParams())// 
//        this.stopLoading();
      },
      /**
       * 
       */
      reload() {
        this.initData();
      },
      /**
       * 
       */
      startLoading(){
        if (this.mode == 'pen') {
          this.listloading = true;
        }
        if (this.mode == 'infinite') {
          this.isMore = true;
        }
        this.loading = true;
      },
      stopLoading(){
        this.loading = false;
        this.listloading = false;
        this.isMore = false;
      },
      handleSizeChange(limit) {
//        console.log(` ${limit} `);
      },
      handleCurrentChange(pageNum) {
        this.toPage(pageNum);
      },
      /**
       * 
       * list
       */
//      addToList(list){
//        if (productList && productList.length > 0) {
//          this.dataList.push.apply(this.dataList, list);
//        }else {
//            this.currentPage--;
//        }
//      },
      //
      doCheck(){
        let scrollTop = this.getScrollTop();
        let offsetHeight = this.getVisibleHeight();
        let scrollHeight = this.getScrollHeight();
        var scrollBottom = 240;//
        let downTrigger = scrollTop + offsetHeight >= scrollHeight - 240;
        if (downTrigger && this.hasNextPage) {
          this.loadMore();
        }
      },
      getScrollTop(event, el) {
        var scrollTop = el ? el.scrollTop : (document.documentElement.scrollTop || document.body.scrollTop);
        return scrollTop;
      },
      //
      getVisibleHeight(event, el){
        let height = el ? el.offsetHeight : window.innerHeight;
        return height;
      },
      //
      getScrollHeight(event, el) {
        let height = el ? el.scrollHeight : (document.documentElement.scrollHeight || document.body.scrollHeight);
        return height;
      },
      /**
       * 
       * :(wait)(scroll,resize)func
       * @param func 
       * @param func 
       * */
      debounce(func, wait, immediate) {
        var timeout;
        return function () {
          var context = this, args = arguments;
          var later = function () {
            timeout = null;
            if (!immediate) func.apply(context, args);
          };
          var callNow = immediate && !timeout;
          clearTimeout(timeout);
          timeout = setTimeout(later, wait);
          if (callNow) func.apply(context, args);
        };
      }
    }
  }
</script>

<style scoped>
  .loadList {
    min-height: 200px;
  }

  .load-more {
    margin-top: 40px;
  }

  .load-more button{
    margin: 25px auto;
    display: block;
  }

  .p-list-con {
    margin-top: 15px;
    margin-right: -20px;
    overflow: hidden;
  }

  .p-list-con .err-tip {
    padding: 10px;
    font-size: 14px;
    color: #f60;
    background: #eee;
  }
</style>




